/////////////////////////////////////////
// USER SCHEMA
/////////////////////////////////////////

export const UserSchema = z.object({
  status: UserStatusSchema,
  role: UserGroupSchema,
  id: z.number().int(),
  createdAt: z.date(),
  updatedAt: z.date(),
  isDeleted: z.boolean(),
  username: z.string(),
  hashedPassword: z.string().nullable(),
  hashedRefreshToken: z.string().nullable(),
}).openapi({"display_name":"员工"})

export type User = z.infer<typeof UserSchema>

// USER RELATION SCHEMA
//------------------------------------------------------

export type UserRelations = {
  profile?: UserProfileWithRelations | null;
  incomingInspectedRecords: IncomingInspectRecordWithRelations[];
  incomingReviewedRecords: IncomingInspectRecordWithRelations[];
  inProcessInspectedRecord: InProcessInspectRecordWithRelations[];
  inProcessReviewedRecord: InProcessInspectRecordWithRelations[];
};

export type UserWithRelations = z.infer<typeof UserSchema> & UserRelations

export const UserWithRelationsSchema: z.ZodObject<any> = UserSchema.merge(z.object({
  profile: z.lazy(() => UserProfileWithRelationsSchema).nullable(),
  incomingInspectedRecords: z.lazy(() => IncomingInspectRecordWithRelationsSchema).array(),
  incomingReviewedRecords: z.lazy(() => IncomingInspectRecordWithRelationsSchema).array(),
  inProcessInspectedRecord: z.lazy(() => InProcessInspectRecordWithRelationsSchema).array(),
  inProcessReviewedRecord: z.lazy(() => InProcessInspectRecordWithRelationsSchema).array(),
}))

