import {
  EquipmentWithRelationsSchema,
  IncomingInspectRecordSchema,
  InProcessInspectRecordSchema,
  MaterialSchema,
  MaterialSpecInspectSchema,
  MaterialSpecWithRelationsSchema,
  OperationSpecInspectSchema,
  OperationSpecSchema,
  OperationWithRelationsSchema,
  ProductLineWithRelationsSchema,
  RepairMaterialInventoryWithRelationsSchema,
  RepairMaterialSchema,
  RepairPlanWithRelationsSchema,
  RepairRecordWithRelationsSchema,
  TaskFormRelationSchema,
  UserSchema,
} from '@flowda-projects/prisma-wms'
import { meta } from '@flowda-projects/flowda-shared'
import { z } from 'zod'

export const UserResourceSchema = UserSchema.omit({
  isDeleted: true,
  hashedPassword: true,
  hashedRefreshToken: true,
}).extend({
  __meta: meta({
    extends: 'UserSchema',
  }),
})

export const TaskFormRelationResourceSchema = TaskFormRelationSchema.omit({
  isDeleted: true,
}).extend({
  __meta: meta({
    extends: 'TaskFormRelationSchema',
  }),
})

// prisma:false camunda API
export const TaskResourceSchema = z
  .object({
    id: z.string().cuid(),
    name: z.string(),
    assignee: z.string(),
    created: z.date(),
    executionId: z.string(),
    processDefinitionId: z.string(),
    processInstanceId: z.string(),
    taskDefinitionKey: z.string(),
    variables: z.string(),
  })
  .extend({
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '任务',
    display_column: 'name',
    display_primary_key: 'true',
  })

export const ProcessDefinitionResourceSchema = z
  .object({
    id: z.string(),
    key: z.string(),
    name: z.string(),
    version: z.number(),
    resource: z.string(),
    deploymentId: z.string().cuid(),
    suspended: z.boolean(),
    historyTimeToLive: z.number(),
    startableInTasklist: z.boolean(),
  })
  .extend({
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '已部署流程图',
    display_column: 'name',
    display_primary_key: 'true',
  })

export const WorkflowUserResourceSchema = z
  .object({
    id: z.string(),
    firstName: z.string(),
    lastName: z.string(),
    email: z.string(),
  })
  .extend({
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '用户',
    display_column: 'id',
  })

// custom form
export const AddToAdminResourceSchema = UserSchema.omit({
  isDeleted: true,
  hashedPassword: true,
  hashedRefreshToken: true,
  status: true,
  createdAt: true,
  updatedAt: true,
  username: true,
})
  .extend({
    uid: z.number().openapi({ title: '申请人 ID', access_type: 'read_only' }),
    username: z.string().openapi({ title: '申请人用户名', access_type: 'read_only' }),
    __meta: meta({
      extends: 'UserSchema',
    }),
  })
  .openapi({
    primary_key: 'id',
    display_name: '申请管理员表单',
    display_column: 'username',
    display_primary_key: 'true',
  })

// 表单
export const ManagerApproveSchema = z
  .object({
    comment: z.number().openapi({ title: '意见' }),
    __meta: meta({
      prisma: false,
    }),
  })
  .openapi({})

